.Input__Input__-base64-5- {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.Input__Label__-base64-5- {
  font-weight: bold;
  display: block;
  margin-bottom: 8px;
}

.Input__InputElement__-base64-5- {
  outline: none;
  border: 1px solid #ccc;
  background-color: white;
  font: inherit;
  padding: 6px 10px;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.Input__InputElement__-base64-5-.Input__Invalid__-base64-5- {
  border: 1px solid red;
  background-color: #fda49a;
}
.Input__InputElement__-base64-5-:focus {
  outline: none;
  background-color: #ccc;
}

.Auth__Auth__-base64-5- {
    margin: 20px auto;
    width: 80%;
    text-align: center;
    box-shadow: 0 2px 3px #ccc;
    border: 1px solid #eee;
    padding: 10px;
    box-sizing: border-box;
}

.Auth__Input__-base64-5- {
    display: block;
    margin: 0 auto;
}

@media (min-width: 600px) {
    .Auth__Auth__-base64-5- {
        width: 500px;
    }
}
